import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'


// import Lightbox from 'react-images'
import Gallery from "../components/Gallery"

import thumb01 from '../assets/images/thumbs_ML/kit-mochila.jpg'
import thumb02 from '../assets/images/thumbs_ML/kit-mochila 2.jpg'
import thumb03 from '../assets/images/thumbs_ML/kit-mochila 3.jpg'
import thumb04 from '../assets/images/thumbs_ML/kit-mochila 4.jpg'
import thumb05 from '../assets/images/thumbs_ML/kit-mochila 5.jpg'

import full01 from '../assets/images/fulls/kit-mochila.jpg'
import full02 from '../assets/images/fulls/kit-mochila 2.jpg'
import full03 from '../assets/images/fulls/kit-mochila 3.jpg'
import full04 from '../assets/images/fulls/kit-mochila 4.jpg'
import full05 from '../assets/images/fulls/kit-mochila 5.jpg'

const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, caption: 'Photo 1', description: 'Set De Jardín De Infantes Personalizado En Quilmes Cole Kit'},
    { id: '2', src: full02, thumbnail: thumb02, caption: 'Photo 2', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '3', src: full03, thumbnail: thumb03, caption: 'Photo 3', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '4', src: full04, thumbnail: thumb04, caption: 'Photo 4', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '5', src: full05, thumbnail: thumb05, caption: 'Photo 5', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'}
];

class HomeIndex extends React.Component {

    

    render() {
        //const siteTitle = this.props.data.site.siteMetadata.title
        //const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                        <title>hi</title>
                        <meta name="description" />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header className="major">
                            <h2>Set De Jardín De Infantes Personalizado Con Mochila</h2>
                            <h4>$225</h4> 
                        </header>
                        <p>DESCRIPCION: <br />
                        * Set de jardin sublimado con motivos personalizados a eleccion.<br />
                        * Consta de: <br />
                        - Mantel 35 x 40 cm<br />
                        - Servilleta 25 x 25 cm<br />
                        - Toalla 20 x 25 cm<br />
                        - Mochila de Tela de 25 x 35 cm<br />
                        Medidas Aproximadas.<br />
                        Todo Personalizado a eleccion con imagen y/o foto, nombre y todo ribeteado con cinta al bies<br />
                        * Materiales: tropical mecanico.</p>
                        <ul className="actions">
                        </ul>
                        <a mp-mode="dftl" href="https://www.mercadopago.com/mla/checkout/start?pref_id=8068205-16a3e29c-0250-43a7-9b0c-e0078357bcaf" name="MP-payButton" className='blue-ar-l-rn-none preview__button'>Pagar</a><br /><br /> 
                        <Link to="/">Volver</Link>
                        <br /><br />    

                    </section>

                    <div id="two"> 
                        <h2></h2>
                    
                        <Gallery images={DEFAULT_IMAGES.map(({ id, src, thumbnail, caption, description }) => ({
                            src,
                            thumbnail
                        }))} />

                        <ul className="actions">
                            <li><a href="#" className="button"></a></li>
                        </ul>
                    </div>

                    

                </div>

            </div>
        )
    }
}

export default HomeIndex



